'use client';
import React, { useState, useEffect } from "react";
import { useRouter } from 'next/navigation';
import { fetchDepartments } from "../Services/departmentService";
import { fetchSelectedRoomDepartments, addRoomToDepartment, fetchAvailableRoomDepartments } from "../Services/RoomDepartmentService";

const RoomsDepartment = () => {
  const [departments, setDepartments] = useState([]);
  const [selectedDepartmentId, setSelectedDepartmentId] = useState("");
  const [roomDepartments, setRoomDepartments] = useState([]);
  const [availableRoomDepartments, setAvailableRoomDepartments] = useState([]);
  const [selectedRoomId, setSelectedRoomId] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [actionStatus, setActionStatus] = useState({ type: null, message: null });
  const router = useRouter();

  useEffect(() => {
    const loadDepartments = async () => {
      try {
        setLoading(true);
        const allDepartments = await fetchDepartments();
        setDepartments(allDepartments || []);
      } catch (err) {
        console.error("Error fetching departments:", err);
        setError("Failed to load departments. Please try again.");
      } finally {
        setLoading(false);
      }
    };

    loadDepartments();
  }, []);

  const fetchRoomDepartmentData = async (departmentId) => {
    if (!departmentId) {
      console.error("Invalid departmentId:", departmentId);
      return; // Prevent API calls with undefined departmentId
    }

    try {
      setLoading(true);
      setError(null);

      const [departmentRooms, allAvailableRooms] = await Promise.all([
        fetchSelectedRoomDepartments(departmentId),
        fetchAvailableRoomDepartments(departmentId) // Ensure departmentId is passed here
      ]);

      setRoomDepartments(departmentRooms || []);
      setAvailableRoomDepartments(allAvailableRooms || []);
    } catch (err) {
      console.error("Error fetching room departments:", err);
      setError("Failed to load room departments. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (selectedDepartmentId) {
      fetchRoomDepartmentData(selectedDepartmentId);
    }
  }, [selectedDepartmentId]);

  const handleDepartmentChange = (e) => {
    setSelectedDepartmentId(e.target.value);
    setSelectedRoomId("");
  };

  const handleAddRoom = async () => {
    if (!selectedRoomId) {
      setActionStatus({ type: 'error', message: 'Please select a room first' });
      setTimeout(() => setActionStatus({ type: null, message: null }), 3000);
      return;
    }

    try {
      setActionStatus({ type: 'loading', message: 'Adding room...' });

      await addRoomToDepartment(selectedDepartmentId, selectedRoomId);
      await fetchRoomDepartmentData(selectedDepartmentId);
      setSelectedRoomId("");
      setActionStatus({ type: 'success', message: 'Room added successfully' });
      setTimeout(() => setActionStatus({ type: null, message: null }), 3000);
    } catch (err) {
      console.error("Error adding room:", err);
      setActionStatus({ type: 'error', message: 'Failed to add room. Please try again.' });
    }
  };

  return (
    <div className="p-4 sm:p-6 mx-auto bg-gray-50 min-h-screen w-full">
      <div className="mb-6 flex flex-col sm:flex-row justify-between items-start sm:items-center">
        <div>
          <h3 className="text-xl text-green-900 font-bold">Room Department Management</h3>
          <p className="text-sm text-gray-600">Manage room assignments for departments</p>
        </div>
        <button
          onClick={() => router.back()}
          className="mt-3 sm:mt-0 px-4 py-2 bg-gray-600 text-white rounded-md text-sm font-medium hover:bg-gray-700 transition-colors"
        >
          Back to Dashboard
        </button>
      </div>

      <div className="bg-white rounded-lg shadow-md overflow-hidden mb-6">
        <div className="p-6 border-b border-gray-200">
          <h4 className="text-md font-semibold text-gray-800 mb-4">Select a Department</h4>
          {loading && !selectedDepartmentId ? (
            <div className="flex items-center justify-center py-4">
              <svg className="animate-spin h-5 w-5 text-indigo-500 mr-2" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
              </svg>
              <span className="text-gray-600">Loading departments...</span>
            </div>
          ) : (
            <select
              value={selectedDepartmentId}
              onChange={handleDepartmentChange}
              className="w-full px-4 py-2 border rounded-md text-sm text-gray-900 bg-white focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
            >
              <option value="">-- Select a Department --</option>
              {departments.map((dept) => (
                <option key={dept.departmentId} value={dept.departmentId}>
                  {dept.departmentName}
                </option>
              ))}
            </select>
          )}
        </div>
      </div>

      {selectedDepartmentId && (
        <>
          <div className="bg-white rounded-lg shadow-md overflow-hidden mb-6">
            <div className="p-6 border-b border-gray-200">
              <h4 className="text-md font-semibold text-gray-800 mb-4">Add Room to Department</h4>
              <div className="flex flex-col sm:flex-row gap-4">
                <select
                  value={selectedRoomId}
                  onChange={(e) => setSelectedRoomId(e.target.value)}
                  className="flex-1 px-4 py-2 border rounded-md text-sm text-gray-900 bg-white focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                  disabled={availableRoomDepartments.length === 0}
                >
                  <option value="">Select an available room</option>
                  {availableRoomDepartments.map((room) => (
                    <option key={room.roomId} value={room.roomId}>
                      {room.roomName} (Capacity: {room.roomCapacity})
                    </option>
                  ))}
                </select>
                <button
                  onClick={handleAddRoom}
                  disabled={!selectedRoomId}
                  className="px-6 py-2 bg-indigo-600 text-white rounded-md text-sm font-medium hover:bg-indigo-700 disabled:bg-indigo-300 disabled:cursor-not-allowed transition-colors"
                >
                  Add Room
                </button>
              </div>
              {availableRoomDepartments.length === 0 && (
                <p className="mt-3 text-sm text-gray-500">No available rooms to add.</p>
              )}
            </div>
          </div>

          <div className="bg-white rounded-lg shadow-md overflow-hidden">
            <div className="p-6">
              <h4 className="text-md font-semibold text-gray-800 mb-4">Rooms in Selected Department</h4>
              {roomDepartments.length > 0 ? (
                <div className="overflow-x-auto ring-1 ring-gray-200 rounded-lg">
                  <table className="min-w-full divide-y divide-gray-200">
                    <thead className="bg-gray-50">
                      <tr>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Room Name</th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Capacity</th>
                      </tr>
                    </thead>
                    <tbody className="bg-white divide-y divide-gray-200">
                      {roomDepartments.map((room) => (
                        <tr key={room.roomId} className="hover:bg-gray-50">
                          <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-700">
                            {room.roomName}
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-600">
                            {room.roomCapacity}
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              ) : (
                <div className="text-center p-8 bg-gray-50 rounded-lg border border-gray-200">
                  <svg className="mx-auto h-12 w-12 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z" />
                  </svg>
                  <h3 className="mt-2 text-sm font-medium text-gray-900">No rooms</h3>
                  <p className="mt-1 text-sm text-gray-500">No rooms assigned to this department yet.</p>
                </div>
              )}
            </div>
          </div>
        </>
      )}

      {error && (
        <div className="mb-6 p-4 bg-red-100 text-red-700 rounded-lg border border-red-200 flex items-center">
          <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-2 flex-shrink-0" viewBox="0 0 20 20" fill="currentColor">
            <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
          </svg>
          {error}
        </div>
      )}

      {actionStatus.message && (
        <div className={`mb-6 p-3 rounded-lg text-xs flex items-center ${
          actionStatus.type === 'error' ? 'bg-red-100 text-red-700 border border-red-200' : 
          actionStatus.type === 'success' ? 'bg-green-100 text-green-700 border border-green-200' :
          'bg-blue-100 text-blue-700 border border-blue-200'
        }`}>
          {actionStatus.message}
        </div>
      )}
    </div>
  );
};

export default RoomsDepartment;

